<template>
  <div class="rank">
    <van-nav-bar
      fixed
      left-text="返回"
      left-arrow
      @click-left="onClickLeft"
    />
    <div class="photo" ref="photo"
      :style="{transform: `scale(${scale})`}">
      <div :style="{backgroundImage: getPic}"></div>
    </div>
    <scroll-view class="wrap" ref="wrap" @scroll="onScroll">
      <van-skeleton
        title
        :row="10"
        :loading="loading"
      >
        <div v-if="topInfo" class="list">
          <div class="list-title">
            <h2>{{topInfo.ListName}}</h2>
            <p>{{topInfo.listennum}}</p>
          </div>
          <div class="list-item" v-for="(e, i) in songList" :key="i" @click="start(i)">
            <p class="order">{{1 + i}}</p>
            <div class="list-info">
              <h3>{{e.data.songname}}</h3>
              <p>{{e.data.singer[0].name}}</p>
            </div>
          </div>
        </div>
        <div v-else class="err">
          加载失败
        </div>
      </van-skeleton>
    </scroll-view>
  </div>
</template>
<script>
import { mapGetters, mapState, mapActions, mapMutations } from 'vuex'
import ScrollView from '#/ScrollView'
export default {
  name: 'RankDetail',
  components: {
    ScrollView,
  },
  data() {
    return {
      rankData: null,
      top: 0,
      scale: 1,
      rankId: '',
    }
  },
  computed: {
    ...mapState('music', ['loading']),
    ...mapGetters('music', ['songList', 'topInfo']),
    getPic() {
      return this.topInfo ? `url(${this.topInfo.pic_album})` : ''
    },
  },
  beforeRouteEnter (to, from, next) {
    next(vm => {
      vm.$store.dispatch('music/rankSongs', to.params.id)
    })
  },
  mounted() {
    this.top = this.$refs.photo.getBoundingClientRect().height
  },
  methods: {
    ...mapActions('player', ['play']),
    ...mapMutations('player', ['syncList']),
    onClickLeft() {
      this.$router.back()
    },
    onScroll({ y }) {
      if (y > 0) {
        this.scale = (this.top + y) / this.top
      }
    },
    start(i) {
      let { id } = this.$router.currentRoute.params
      if (this.rankId !== id) {
        this.syncList()
        this.rankId = id
      }
      this.play(i)
    }
  }
}
</script>
<style lang="scss" scoped>
/deep/ .van-skeleton__row {
  height: .6rem; }
/deep/ .van-nav-bar {
  background-color: #272727;
  opacity: 0.8; }
/deep/ .van-hairline--bottom::after {
  border: none; }
/deep/ .van-nav-bar__text {
  color: #fff; }
/deep/ .van-nav-bar .van-icon {
  color: #fff; }
.photo {
  transform-origin: top;
  div {
    top: 0;
    padding-top: 70%;
    background-size: cover;} }
.rank {
  background-color: #272727; }
.wrap {
  height: 60vh;
  .list {
    position: relative;
    background-color: #272727;
    color: #fff;
    &-title {
      h2 {
        padding: .15rem; }
      p {
        font-size: 16px;
        padding: .15rem; }
    }
    &-item {
      position: relative;
      height: 55px;
      font-size: 16px;}
    .order {
      position: absolute;
      font-size: 16px;
      top: 50%;
      left: .5rem;
      transform: translate(-50%, -50%); }
    &-info {
      position: relative;
      margin-left: 1.1rem;
      h3 {
        padding: .15rem 0; }
      p {
        font-size: 12px;
        color: #aaa; } } } 
  .err {
    position: absolute;
    width: 100%;
    text-align: center; }}
</style>


